import { Fragment, useState } from "react";
import { useAppSelector } from "../../store/hooks";
import Loader from "../ui/Loader";

const Result = () => {
  const addressState = useAppSelector((state) => state.address);

  const [isLoaded, setIsLoaded] = useState(false);

  if (!isLoaded) {
    setTimeout(() => {
      setIsLoaded(true);
    }, 3000);
    return (
      <Fragment>
        <Loader>Calculating results...</Loader>
      </Fragment>
    );
  }

  const c1: string = process.env.REACT_APP_GOOGLE_MAPS_API_KEY as string;
  console.log("1", c1);
  const c2: string = process.env.GOOGLE_MAPS_API_KEY as string;
  console.log("2", c2);

  console.log("3", process.env);

  console.log("4", process.env.local);

  return <p>results here</p>;
};

export default Result;
